<template>
    <div class="results-container">
        <b-row>
            <b-col md="12">
                <b-nav-form class="mb-4 search-bar">
              <b-form-input size="sm" class="mr-sm-2" placeholder="Search" v-model="name" :state="validName"></b-form-input>
              <button class="my-2 my-sm-0 btn-brand" type="submit" @click="goSearchResults">Search</button>
        </b-nav-form> 
                <div v-if="rests">
                    <h2>All Results</h2>
                    <div>
                        <b-dropdown id="dropdown-1" text="All ratings" ref="ratingDropdown">
                        <b-dropdown-item>All ratings</b-dropdown-item>
                        <b-dropdown-item>Highest rated by region</b-dropdown-item>
                        <b-dropdown-item>Lowest rated by region</b-dropdown-item>
                        </b-dropdown>
                        <b-dropdown id="dropdown-2" text="Review count">
                        <b-dropdown-item>All reviews</b-dropdown-item>
                        <b-dropdown-item>More than 5 reviews</b-dropdown-item>
                        <b-dropdown-item>More than 10 reviews</b-dropdown-item>
                        </b-dropdown>
                        <b-dropdown id="dropdown-3" text="Any payment options">
                        <b-dropdown-item>Cash</b-dropdown-item>
                        <b-dropdown-item>Visa</b-dropdown-item>
                        <b-dropdown-item>Mastercard</b-dropdown-item>
                        <b-dropdown-item>Alipay</b-dropdown-item>
                        <b-dropdown-item>Zhifubao</b-dropdown-item>
                        <b-dropdown-item>All payment options</b-dropdown-item>
                        </b-dropdown>
                    </div>
                    <div v-for="each in rests" :key="each.id">
                    <SingleRes :rest="each"></SingleRes>
                    </div>
                </div>
                <div v-else>
                    <div class="text-center text-danger my-2">
                        <b-spinner class="align-middle"></b-spinner>
                        <strong>Loading...</strong>
                    </div>
                </div>
                The region where the average rating of its restaurants is the highest: 
            </b-col>
        </b-row> 
    </div>
  </template>
  
  <script>
import { ref, watch, watchEffect } from 'vue'
import SingleRes from '../components/SingleRes.vue'
import { useRouter } from 'vue-router'

    export default {
        components: { SingleRes },
        props: ['name'],
        setup(props) {
            const isBusy = ref(false)

            const rests = [
                {name: 'Burger King', category: 'Fastfood', stars: 4.5, numberReviews: 1, id: 1},
                {name: 'Ad King', category: 'Fastfood', stars: 4.5, numberReviews: 2, id: 2}
            ]

            const router = useRouter()


            const validName = ref(null)
            const goSearchResults = () => {
            validName.value = null
            if (props.name && props.name) {
                router.push({ name: 'searchResults', params: { name: props.name}})
            } else {
                validName.value = false
            }
            }

            const ratingDropdown = ref(null)

            watchEffect(() => {
                if (props.name && props.name.length) {
                    validName.value = null
                }
            })
 

            return {
                isBusy,
                rests,
                goSearchResults,
                validName,
                ratingDropdown
            }
        },
    }
  </script>

  <style scoped>

    .results-container {
        padding: 40px;
        padding-top: 0;
        font-family: 'Josefin Sans', sans-serif;
        margin-bottom: 100px;
    }

    .search-bar {
        width: 100%;
        margin: 20px 0;
    }

  </style>

  <style>
     #dropdown-1, #dropdown-2, #dropdown-3 {
        background: rgba(195,144,127,1);
        border: none;
        margin-bottom: 20px;
        margin-right: 5px;
    }
</style>